import React, { Component } from "react";
import Pembayaran from "../Images/PembayaranPerBon.svg";
import Tolak from "../Images/tolakanCustomer.svg";
import Titipan from "../Images/TitipanCustomer.svg";
import transfer from "../Images/transferBalance.svg";
import validasi from "../Images/validasiTransfer.svg";
import { Link } from "react-router-dom";
import Skeleton from "react-loading-skeleton";

class KlasifikasiCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
    };
    this.test = this.test.bind(this);
  }

  test() {
    this.setState({
      show: true,
    });
  }

  render() {
    setTimeout(this.test, 1000);
    return (
      <div className="container-fluid" style={{ width: "100%" }}>
        <div className="row">
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={Pembayaran} width="80%" alt="Kode Barang" />
                  <h4>
                    Pembayaran Per <br /> Bon
                  </h4>
                  <Link to="/ayugold.com/PembayaranPerBon">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={Tolak} width="80%" alt="Kode Barang" />
                  <h4>
                    Tolak Pembayaran <br /> Customer
                  </h4>
                  <Link to="/ayugold.com/TolakanPembayaranCustomer">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={Titipan} width="80%" alt="Kode Barang" />
                  <h4>
                    Titipan Gr <br />
                    Customer
                  </h4>
                  <Link to="/ayugold.com/TitipanGrCustomer">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={transfer} width="80%" alt="Kode Barang" />
                  <h4>
                    Transfer Balance <br />
                    Customer
                  </h4>
                  <Link to="/ayugold.com/TransferBalance">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-12">
            <br />
          </div>
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={validasi} width="80%" alt="Kode Barang" />
                  <h4>
                    Validasi Transfer <br /> Customer
                  </h4>
                  <Link to="/ayugold.com/ValidasiTransferCustomer">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-3">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={validasi} width="80%" alt="Kode Barang" />
                  <h4>
                    Validasi Pembayaran Giro <br /> Customer
                  </h4>
                  <Link to="/ayugold.com/ValidasiPembayaranGiro">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default KlasifikasiCustomer;
